<template><section><html><head></head><body><h1>PostProcessStageCollection</h1>
<p>The <code v-pre="">vc-collection-stage-process-post</code> component is used to load a collection of PostProcessStages. Note that this component will replace <code v-pre="">scene.postProcessStages</code> directly.</p>
<h2>Example</h2>
<h3>Load PostProcessStageCollection</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-collection-stage-process-post>
          <vc-stage-process-post :fragment-shader="fsRain"></vc-stage-process-post>
          <vc-stage-process-post :fragment-shader="fsScanSegment" :uniforms="uniforms"></vc-stage-process-post>
        </vc-collection-stage-process-post>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
