        <span class="hljs-keyword">var</span> _Cartesian4Center1 = <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">Cartesian4</span>(_Cartesian3Center1.<span class="hljs-property">x</span>, _Cartesian3Center1.<span class="hljs-property">y</span>, _Cartesian3Center1.<span class="hljs-property">z</span>, <span class="hljs-number">1</span>)
